import React, { Component } from 'react';

export class User extends Component {

  constructor (props) {
      console.log(props)
    super();
    this.props = props;
    this.state = {
      users: props.users,
      user: props.user,
      items: [],
      product: '',
      link: '',
      price: '',
      factor: '',
    }

    this.addItem = this.addItem.bind(this);

  }

  componentWillMount() {
    this.props.required(this.state.user)
    this.getItems();
  }

  async getItems(){
    const response = await this.props.req.get(`/users/${this.state.user._id}`);
    this.setState({items: response.data.items});
  }

  async addItem(event) {

      event.preventDefault();
      const {items, product, link, price, factor} = this.state;

      if (!product || product === '' || !product.length) {
          return false;
      }
      items.push({
        product, link, price, factor
      });

      const response = await this.props.req.post(`/users/${this.state.user._id}`, {
        product, link, price, factor
      })

      this.setState({items: response.data.items});
  }

  handleChange (event) {
      const { state } = this;
      state[event.target.name] = event.target.value;
      this.setState(state);
  }


  render() {

    const {user, items} = this.state;

    if (!user) {
        return (<div />);
    }

    const tableRows = items.map((item, idx) => {
        return (
            <tr key={idx}>
                <td>{item.product}</td>
                <td>{item.link}</td>
                <td>{item.price}</td>
                <td>{item.factor}</td>
            </tr>
        );
    });

    return (
        <section className="box padded">
            <h3>Hey {user.name}!</h3>
            <div className="formWrapper">
                <form onSubmit={this.addItem}>
                    <span>Wat*</span>
                    <input type="text" name="product" required onChange={event => this.handleChange(event)}/>
                    <span>Link</span>
                    <input type="text" name="link" onChange={event => this.handleChange(event)}/>
                    <span>Prijs</span>
                    <select name="price" onChange={event => this.handleChange(event)}>
                        <option selected>‚Ç¨1-‚Ç¨10</option>
                        <option>‚Ç¨10-‚Ç¨25</option>
                        <option>‚Ç¨25-‚Ç¨50</option>
                        <option>‚Ç¨50-‚Ç¨100</option>
                        <option>‚Ç¨100+</option>
                    </select>
                    <span>Hoe graag hebben factor (1 is laag, 5 is hoog)</span>
                    <select name="factor" onChange={event => this.handleChange(event)}>
                        <option>1</option>
                        <option>2</option>
                        <option selected >3</option>
                        <option>4</option>
                        <option>5</option>
                    </select>
                    <button className="btn btnLarge">Opslaan</button>
                </form>
            </div>
            <h3>Jouw huidige lijst:</h3>
            <div className="tableWrapper">
                <table>
                    <thead>
                        <tr>
                            <th>Product</th>
                            <th>Link</th>
                            <th>Prijs</th>
                            <th>Factor</th>
                        </tr>
                    </thead>
                    <tbody>
                        { tableRows }
                    </tbody>
                </table>
            </div>
        </section>
    );
  }
}
